import {CourseActions} from 'core_courseformat/local/content/actions';
import {init as initMutations} from 'format_topics/mutations';

const TopicsActions = {
    /**
     * Handle a highlight section request.
     *
     * @param {Element} target the dispatch action element
     * @param {Event} event the triggered event
     */
    _requestHighlightSection: async function(target, event) {
        this._requestMutationAction(target, event, 'sectionHighlight');
    },

    /**
     * Handle a remove highlight from section request.
     *
     * @param {Element} target the dispatch action element
     * @param {Event} event the triggered event
     */
    _requestUnhighlightSection: async function(target, event) {
        this._requestMutationAction(target, event, 'sectionUnhighlight');
    },
};

export const init = () => {
    CourseActions.addActions(TopicsActions);
    initMutations({
        // Legacy actions.
        setmarker: 'sectionHighlight',
        removemarker: 'sectionUnhighlight',
        // Moodle 4.0+ actions.
        sectionHighlight: 'sectionHighlight',
        sectionUnhighlight: 'sectionUnhighlight',
    });
};
